import React, { Component } from "react";

export default class ProfileCard extends Component {
  render() {
    return (
      <React.Fragment>
        <div class="card card-custom">
          {/* <!--begin::Body--> */}
          <div class="card-body pt-15">
            {/* <!--begin::User--> */}
            <div class="text-center mb-10">
              <div class="symbol symbol-60 symbol-circle symbol-xl-90">
                <div
                  class="symbol-label"
                  style={{backgroundImage:"url('assets/media/users/300_21.jpg')"}}
                ></div>
                <i class="symbol-badge symbol-badge-bottom bg-success"></i>
              </div>
              <h4 class="font-weight-bold my-2">Dr .James Jones</h4>
              <div class="text-muted mb-2">General Physican</div>
              {/* <span class="label label-light-warning label-inline font-weight-bold label-lg">
                Active
              </span> */}
            </div>
            {/* <!--end::User-->
												<!--begin::Contact--> */}
            <div class="mb-10 text-center">
            <span class="fa fa-star checked"></span>
            <span class="fa fa-star checked"></span>
            <span class="fa fa-star checked"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            </div>
            {/* <!--end::Contact-->
												<!--begin::Nav--> */}
            <a
              href="#"
              class="btn btn-hover-light-primary font-weight-bold py-3 px-6 mb-2 text-center btn-block "
            >
              Treated Patients: 130
            </a>
            <a
              href="#"
              class="btn btn-hover-light-primary font-weight-bold py-3 px-6 mb-2 text-center btn-block"
            >
              Happy patients: 120
            </a>
            <a
              href="#"
              class="btn btn-hover-light-primary font-weight-bold py-3 px-6 mb-2 text-center btn-block"
            >
              Reward Points: 20
            </a>
            <a
              href="#"
              class="btn btn-hover-light-primary font-weight-bold py-3 px-6 mb-2 text-center btn-block"
            >
              Badge: Gold
            </a>
            <a
              href="#"
              class="btn btn-hover-light-primary font-weight-bold py-3 px-6 mb-2 text-center btn-block"
            >
              Balance: Rs. 20000
            </a>
            {/* <!--end::Nav--> */}
          </div>
          {/* <!--end::Body--> */}
        </div>
      </React.Fragment>
    );
  }
}
